import { put } from 'redux-saga/effects';
// import feathers from 'feathers';
// import restClient from '@feathersjs/rest-client';
// import authentication from 'feathers-authentication-client';

import axios from 'axios';

// const app = feathers();
// app.configure(restClient.axios(axios)).configure(
// 	authentication({
// 		storage: window.localStorage
// 	})
// );

function* login() {
	console.log('logging in');
	try {
		// const response = yield app.authenticate({ storage: window.localStorage || 'cookie' });
		const response = yield axios.post('http://localhost:3001/users', {});
		put({ type: 'AUTHENTICATION_SUCCESS', payload: response });
		console.log('got a response:', response);
	} catch (e) {
		put({ type: 'AUTHENTICATION_FAILED', error: e });
		console.log('got an error:', e);
	}
}

export default login;
